/** @jsx jsx */
import { css, jsx } from "@emotion/react"
import {Light} from '../images/Light'

const headingStyles = css`
  margin-top: 0;
  max-width: 320px;
  font-variant: petite-caps;
`

const headerWrapperStyle = css`
    width: 100%;
    display: flex;
    justify-content: space-between;
`

function swap() {
    const currentBg = getComputedStyle(document.body).getPropertyValue('--bg-color')
    const currentFg = getComputedStyle(document.body).getPropertyValue('--fg-color')

    document.body.style.setProperty(
        '--fg-color',
        currentBg
    );
    document.body.style.setProperty(
        '--bg-color',
        currentFg
    );
}

export function Header() {
    return <div css={headerWrapperStyle}>      
        <h1 css={headingStyles}>
        Joseph D. Phelan
        </h1>
        <div>
            <button onClick={() => swap()}>
                <Light fill={'var(--fg-color)'}/>
            </button>
        </div>
    </div>
}